.make-page-container{
    display:flex;
    flex-direction:row;
    height: 100%;
    width:100%;
}

.make-page-example{
    width:70%;
    height:100%;
    background-color:pink;
}

.colorSelection{
    margin:10px;
    display: flex;
    justify-content : center;
    align-items :center;
    width:90px;
    height:90px;
    background-color:white;
    border-radius: 10px;
    box-shadow: 3px 3px 6px rgba(0,0,0,0.4);
}

.fontSelection{
    margin:10px;
    display: flex;
    justify-content : center;
    align-items :center;
    width:100%;
    height:50px;
    background-color:white;
    border-radius: 3px;
    box-shadow: 3px 3px 6px rgba(0,0,0,0.4);
    cursor:pointer;
}

.fontSelection:hover{
    border:1px 
}

.section-one-make-one{
    display: flex;
    justify-content :center;
    flex-direction:column;
    width:100%;
}

.section-table-container{
    justify-content: start;
    width:80%;
    padding-left: 10px; 
}

.section-selector-container{
    display: flex;
    flex-direction:row;
}

.make-page-section-selector{
    height:40px;
    width:100%;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-top:1px solid rgba(128, 128, 128, 0.473);
    border-left:1px solid rgba(128, 128, 128, 0.473);
    border-right:1px solid rgba(128, 128, 128, 0.473);
    background-color:white;
    cursor:pointer;
    display: flex;
    justify-content :center;
    align-items :center;
}

.section-image{
    width:200px;
    height:200px;
}

.section-one-make{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items : start;
    padding-top:10px;
}
.section-one-title-input{
    width:100%;
    font-size: 19px;
}

.file-input{
    width:30%;
}
.col-image{
    width:300px;
}

.make-navigation{
    width:100%;
    background-color:rgb(255, 255, 255);
    height:30px;
    position:fixed;
    margin-top:-15px;
}

.make-footer{
    width:100%;
    background-color:rgb(133, 133, 133);
    height:90px;
    display: flex;
    justify-content :center;
    align-items :center;
    font-family: 'Noto Sans KR';
}

.last-section-container{
    display: flex;
    justify-content : center;
    align-items : center;
}

.make-navigation{
    position: fixed;
    top: 65px;
    z-index:3;
    height:60px;
    width: 70%;
    display: flex;
    justify-content: start;
    align-items : center;
    background-color:rgba(255, 255, 255, 1);
}

.make-apply-button{
    margin-top:2%;
    cursor: pointer;
    border: 5px solid white;
    background-color:white;
    border-radius:5px;
    color:black;
    width:250px;
    height:40px;
    display: flex;
    align-items:center;
    justify-content : center;
    font-size: 15px;
    text-decoration: none;
    font-family: 'OTWelcomeRA';
}

.make-apply-button:hover{
    background-color:white;
    color:black;
    font-weight:600;
}

.section-title{
    font-size:20px;
    color:#6B63F7;
    font-family:'OTWelcomeRA'; 
    font-weight:700; 
    display:flex; 
    justify-content:space-between; 
    width:100%;
}

.make-nav-bar-title{
    font-size:36px;
    font-weight: 700;
    padding-left:25%;
}

.make-footer{
    background-color:rgb(255, 255, 255);
    height:200px;
    display: flex;
    justify-content : center;
    align-items : center;
    color:rgb(131, 131, 131);
    font-weight:400;
    flex-direction: row;
    width:100%;
    padding-left:25%;
  }