.questionspage-container{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: 'OTWelcomeBA';
}

.q-inner{
    background-color: white;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    font-family: OTWelcomeRA;
    width:60%;
}

.q-title-top{
    text-align: left;
    font-family: OTWelcomeBA;
    font-size:23px;
    margin-top:10%;
}

.tab-one{
    cursor: pointer;
    color: gray;
    margin-left:4%;
}

.q-button{
    margin-top:6%;
    border:2px solid #6a63f7;
    border-radius: 20px;
    width: 150px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color:#6a63f7;
    font-size:17px;
    font-weight: 500;
    padding-top:3%;
}
.q-button:hover{
    background-color: #6a63f733;
}

.quetions-content-bottom{
    width:100%;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    margin-top: 4%;
}

.q-tabs{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: row;
    border-bottom: 1px solid rgba(128, 128, 128, 0.384);
}
.q-one-content{
    margin-top: 2%;
    background-color: rgba(235, 235, 235, 0.3);
    padding:20px 30px;
    border-radius: 30px;
    text-align: left;
    width:100%;
    box-shadow:1px 1px 4px rgba(0,0,0,0.4);
}

.q-content-ask{
    font-weight: 700;
    font-size:17px;
    margin-top:3%;
}
.q-content-answer{
    font-weight: 500;
    font-size:17px;
    margin-top:2%;
}


@media screen and (max-width:800px){
    .nav-button{
        margin-left:1vw;
    }

}