.make-page-nav{
    height: 50px;
    background-color: white;
    width: 100%;
    margin-top:-50px;
    position:fixed;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #6b6b6b;
    font-family: Noto Sans KR;
    font-size: 14px;
}

.make-page-nav-half{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.make-nav-left-text{
    color:black;
    margin-left:2vw;
    cursor: pointer;
    padding:3px 0px;
    border-radius: 5px;
    text-decoration: none;
    padding:3px 10px;
}
.make-nav-left-text:hover{
    color:black;
    background-color: #6a63f7b0;
}
